<template>
  <div class="login" @keyup.enter="handleLogin">
    <div class="login-content flex-row animate__animated animate__fadeIn">
      <div class="login-cut animate__animated animate__slideInLeft"></div>
      <div class="form-wrapper animate__animated animate__slideInRight">
        <div class="title">
          &nbsp;&nbsp;&nbsp;统一登录平台&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        </div>
        <div class="empty-block" />
        <div class="logo">
          <!-- <img src="@/assets/img/silverMagic.png" alt="" /> -->
        </div>
        <div class="operations">
          <div class="box-center">
            <el-button type="primary" class="form-btn" @click="handleLogout">注销</el-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import API from '@/api/index.js'

export default {
  name: 'index',
  data() {
    return {
      form: {
        account: '',
        password: ''
      },
      activeName: 'account',
      isAutoLogin: false,
      isCurrentIp: false,
      appKey: ''
    }
  },
  created() {
    //     if(!localStorage.getItem('token')){
    // this.$message.error('登录失效,请重新登录!')
    // setTimeout(()=>{
    // })
    //     }
  },
  mounted() {
    this.getPlatformListPage()
  },
  methods: {
    //过渡页面 随机请求接口验证token是否失效
    getPlatformListPage() {
      API.getPlatformList().then(res => {
        if (res.status == 401) {
          this.$route.push({
            name: 'login'
          })
        }
      })
    },

    handleLogout() {
      API.logout().then(res => {
        if (res.status === 200) {
          localStorage.removeItem('token')
          localStorage.removeItem('appKey')
          this.$router.push({
            name: 'login'
          })
        } else {
          this.$message.error(res.error)
        }
      })
    }
  },
  beforeDestroy() {
    document.onkeyup = () => {}
  }
}
</script>

<style scoped lang="scss">
.login {
  ::v-deep .el-input--prefix .el-input__inner::-webkit-input-placeholder {
    font-weight: 500;
  }
  ::v-deep .el-input--prefix .el-input__inner {
    font-weight: bold;
  }
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  width: 100%;
  min-height: 100%;
  background: url('../../assets/img/loginBg.png') no-repeat 100%;
  background-size: cover;

  .title {
    margin: 15px 0;
    text-align: center;
  }

  .login-content {
    width: 1300px;
    height: 794px;
    overflow: hidden;
    box-sizing: border-box;
    padding: 10px;
    background: #ffffff;
    box-shadow: 6px 13px 50px 12px rgba(213, 215, 223, 0.51);
    border-radius: 20px;

    .login-cut {
      width: 50%;
      height: 100%;
      background: url('../../assets/img/loginCut.png') no-repeat 100%;
      background-size: 100% 100%;
    }

    .form-wrapper {
      padding: 50px;
      padding-top: 100px;
      padding-left: 150px;
      display: flex;
      flex-direction: column;
      align-items: center;

      .title {
        font-size: 26px;
        font-family: PingFang SC;
        font-weight: 800;
        color: #2a4eff;
        opacity: 1;
      }

      ::v-deep.el-tabs {
        margin: 50px 0 10px 0;

        .el-tabs__nav-wrap::after {
          display: none;
        }

        .el-tabs__item,
        .is-active {
          font-size: 18px;
          font-family: PingFang SC;
          font-weight: 800;
          color: #0a1030;
          padding: 0 50px;
        }

        .is-active {
          color: #2160ec;
        }
      }

      .login-form {
        width: 80%;
      }

      .operations {
        width: 80%;

        .helps {
          margin-top: 10px;
          margin-bottom: 50px;
        }

        .go-regist {
          width: 130px;
          margin-top: 10px;

          span:nth-child(1) {
            color: #b4b3be;
          }
        }

        .form-btn {
          width: 160px;
          height: 50px;
          background: #2a4eff;
          opacity: 1;
          border-radius: 4px;

          &:hover {
            opacity: 0.8;
          }
        }
      }
    }
  }

  .empty-block {
    height: 50px;
  }

  .logo {
    margin: 20px 0 30px 0;
    height: 50px;
  }
}
</style>
